<template>
    <Header />
    <h1>Hello {{ name }}, Welcome on Home Page</h1>
    <table border="1px">
        <tr>
            <th>Id</th>
            <th>Name</th>
            <th>Contact No</th>
            <th>Address</th>
            <th>Actions</th>
        </tr>
        <tr v-for="item in restaurant" :key="item.id">
            <td> {{ item.id }} </td>
            <td> {{ item.name }} </td>
            <td> {{ item.contact }} </td>
            <td> {{ item.address }} </td>
            <td>
                <router-link :to="'/update/' + item.id"><button>Update</button></router-link>
                <button @click="deleteRestaurant(item.id)" style="margin-left: 5px; margin-top:5px;">Delete</button>
            </td>
        </tr>
    </table>
</template>

<script>
import Header from './Header.vue';
import axios from 'axios';
export default {
    name: 'Home',
    data() {
        return {
            name: '',
            restaurant: [],
        }
    },
    components: {
        Header,
    },
    methods: {
            async deleteRestaurant(id){
              let result = await axios.delete("http://localhost:3000/restaurant/"+id);
                console.warn(result);
                if(result.status==200){
                    this.loadData()
                }
            },         
        async loadData() {
            let user = localStorage.getItem('user-info');
            this.name = JSON.parse(user).name;
            if (!user) {
                this.$router.push({ name: 'Signup' })
            }
            let result = await axios.get("http://localhost:3000/restaurant");
            // console.warn(result);
            this.restaurant = result.data;
        }
    },
    async mounted() {
        this.loadData();
    }
}
</script>

<style>
table {
    margin: 31px 131px 0px 285px;
    align-items: center;
}

td {
    width: 130px;
    height: 50px;
}
</style>