<template>
<Header/>
    <h1>Hello {{name}}, Welcome On Add Restaurant Page</h1>
    <form class="add">
        <input type="text" name="name" placeholder="Enter Restaurant Name" v-model="restaurant.name"/>
        <input type="text" name="address" placeholder="Enter Restaurant Address" v-model="restaurant.address"/>
        <input type="tel" name="contact" placeholder="Enter Restaurant Contact No" v-model="restaurant.contact"/>
        <button type="button" @click="addRestaurant">Add New Restaurant</button>
    </form>
</template>

<script>
import Header from './Header.vue'
import axios from 'axios'
    export default {
        name:'Add',
         components:{
            Header, 
        }, 
        data(){
            return{
                name:'',
                restaurant:{
                    name:'',
                    address:'',
                    contact:'',
                }
            }
        }, 
        methods:{
            async  addRestaurant(){
                console.log(this.restaurant);
                const result = await axios.post("http://localhost:3000/restaurant", {
                    name: this.restaurant.name,
                    address: this.restaurant.address,
                    contact: this.restaurant.contact,
                });
                if(result.status==201){
                      this.$router.push({name:'Home'});
                }
                // console.log("result");
            }
        },   
         mounted(){
            let user=localStorage.getItem('user-info');
            this.name=JSON.parse(user).name;
            if(!user){
                 this.$router.push({name:'Signup'})
            }
        }
    }
</script>

