<template>
<Header/>
     <h1>Hello User, Welcome On Update Restaurant Page</h1>
    <form class="add">
        <input type="text" name="name" placeholder="Enter Restaurant Name" v-model="restaurant.name"/>
        <input type="text" name="address" placeholder="Enter Restaurant Address" v-model="restaurant.address"/>
        <input type="tel" name="contact" placeholder="Enter Restaurant Contact No" v-model="restaurant.contact"/>
        <button type="button" @click="updateRestaurant">Update Restaurant</button>
    </form>
</template>

<script>
import Header from './Header.vue'
import axios from 'axios'
    export default {
        name:'Update',
         components:{
            Header, 
        }, 
        data(){
            return{
                name:'',
                restaurant:{
                    name:'',
                    address:'',
                    contact:'',
                }
            }
        }, 
        methods:{
          async  updateRestaurant(){
            console.warn(this.restaurant);
                  const result = await axios.put("http://localhost:3000/restaurant/" +this.$route.params.id, {
                    name: this.restaurant.name,
                    address: this.restaurant.address,
                    contact: this.restaurant.contact,
                });
                if(result.status==200){
                      this.$router.push({name:'Home'});
                }
           }
        },
         async mounted(){
            let user=localStorage.getItem('user-info');
            if(!user){
                 this.$router.push({name:'Signup'})
            }
            const result = await axios.get("http://localhost:3000/restaurant/" +this.$route.params.id)
            // console.warn(this.$route.params.id);
            console.log(result.data);
            this.restaurant=result.data;
        }
    }
</script>

